import './App.css';
import {Switch, Route, NavLink,useRouteMatch} from 'react-router-dom';
import PhotoComponent from './Photo/PhotoComponent';
import Contacts from './Contacts/Contacts'
import Post from './Post/Post';
import smoke from './assets/smoke.png'

const HomeComponent = () => {
  return(
    <>
      <div className='homePage'>
        <img src={smoke} alt="" />
        <p>welcome  ღ </p>
        <p className="homepage_text">= to my homepage =</p>
      </div>
      </>
  )
  }

function App() {
  const match = useRouteMatch()
  return (
    <div className="App">
      <nav>
        <NavLink exact to={`${match.url}`}>Home</NavLink>
        <NavLink to={`${match.url}post`}>Post</NavLink>
        <NavLink to={`${match.url}photo`}>Photo</NavLink>
        <NavLink to={`${match.url}contacts`}>Contacts</NavLink>
      </nav>
      <Switch>
        <Route exact path={`${match.url}`} component={HomeComponent}/>
        <Route path={`${match.url}post`} component={Post}/>
        <Route path={`${match.url}photo`} component={PhotoComponent}/>
        <Route path={`${match.url}contacts`} component={Contacts}/>
      </Switch>
    </div>
  );
}

export default App;
